body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

*, *::after, *::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#logo {
  font-size: 1.5rem;
  font-weight: bold;
}

#header {
  box-sizing: border-box;
  height: 70px;
  padding: 0 7vw 0 7vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #3A3535;
}

#header .area-logo-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 22px;
  font-family: 'Rock Salt', cursive;
  color: #ffc107;
}

#header .area-logo-nav img {
  width: 40px;
  margin-right: 15px;
}

#menu {
  display: flex;
  list-style: none;
  grid: .5rem;
}

#menu .link-menu {
  display: block;
  margin: 0 20px;
  font-size: 18px;
  font-weight: bold;
  text-decoration: none;
  color: white;
  transition: all .30s ease;
}

#menu .link-menu:hover {
  color: #ffc107;
}

#menu .link-button {
  background-color: #ffc107;
  padding: 2px 10px;
  border-radius: 5px;
  color: #3A3535;
}

#menu .link-button:hover {
  background-color: #3A3535;
}

#btn-mobile {
  display: none;
}

@media(max-width: 600px) {
  #header {
    height: 50px;
    padding: 0 3vw 0 7vw;
  }
  #menu {
    display: block;
    position: absolute;
    width: 100%;
    top: 70px;
    right: 0px;
    background: #3a3535e8;
    height: 0px;
    transition: .6s;
    z-index: 1000;
    visibility: hidden;
    overflow-y: hidden;
  }
  #nav.active #menu {
    height: calc(60vh - 70px);
    visibility: visible;
    overflow-y: auto;
  }
  #menu .link-menu {
    padding: 1rem 0;
    margin: 0 1rem;
    border-bottom: 2px solid rgba(0, 0, 0, .05);
  }
  #btn-mobile {
    display: flex;
    padding: .5rem 1rem;
    font-size: 1rem;
    border: none;
    background: none;
    cursor: pointer;
    gap: .5rem;
  }
  #hamburger-button {
    border-top: 2px solid;
    width: 30px;
    color: white;
  }
  #hamburger-button::after, #hamburger-button::before {
    content: '';
    display: block;
    width: 30px;
    height: 2px;
    background: currentColor;
    margin-top: 5px;
    transition: 0.3s;
    position: relative;
  }
  #nav.active #hamburger-button {
    border-top-color: transparent;
  }
  #nav.active #hamburger-button::before {
    transform: rotate(135deg);
  }
  #nav.active #hamburger-button::after {
    transform: rotate(-135deg);
    top: -7px;
  }
  /*outra*/
  .area-index {
    display: grid;
    grid-template-columns: 1fr;
    margin: 0 auto;
    grid-gap: 20px;
  }
  .area-index .info-index {
    width: 90vw;
  }
  .area-img-burguer {
    display: none;
  }
}

/*************************/

.area-index {
  height: 100vh;
  background-color: #3A3535;
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: 0 auto;
}

.area-index .info-index {
  margin-left: 5vw;
  height: 90vh;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.area-index .info-index h3 {
  font-size: 100px;
  font-weight: 600;
}

.area-index .info-index .text-free {
  margin-top: 5vh;
  color: #ffc107;
}

.area-index .info-index .text-burger {
  color: white;
}

.area-index .info-index .info-email {
  font-size: 20px;
  font-weight: bold;
  color: white;
  margin: 20px;
}

.area-index .info-index input, button {
  padding: 13px 15px;
  font-size: 18px;
}

.area-index .info-index button {
  margin-left: -1px;
  border: 1px solid #ffc107;
  background-color: #3A3535;
  cursor: pointer;
  color: #ffc107;
  transition: all .40s ease;
}

.area-index .info-index button:hover {
  background-color: #ffc107;
  color: #3A3535;
}

input:focus {
  box-shadow: 0 0 0 0;
  border: 0 none;
  outline: 0;
}

.back-info {
  height: 20vh;
  font-size: 10px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

/*****************/

.area-index .area-img-burguer {
  background: url(../img/dois-um.png) no-repeat;
  background-position: top center;
  text-align: center;
}

.img-fluid {
  display: flex;
  justify-content: center;
  align-items: center;
}

.img-burguer {
  transform: rotate(-8deg);
  width: 500px;
  margin-top: 80px;
  margin-left: 70px;
}

.sombra {
  margin-top: -60px;
  margin-left: 100px;
}